@font-face{
    font-family:good;
    src: url(assets/roboto-condensed.light.ttf);

}

@font-face{
    font-family:good1;
    src:url(assets/economica-bold.ttf);
}

body{
    text-align:center;
    font-size:20px;
    padding:2.5%;
    font-family: 'good';

}

h1 , h2 , h3 , h4 , h5{
    font-family: 'good1';
}

.track{
    width:250px;
    height:250px;
    position: relative;
    display: inline-block;
    margin: 20px;
    cursor: pointer;
}

.track-image{
    width:250px;
    height:250px;
}

.track-text{
    background-color: black;
    color: white;
    padding: 5px;
    position: absolute;
    width: 250px;
    bottom: 0px;
}

.track-icon{
    position: absolute;
    bottom: 100px;
    left: 100px;
    color:white;
    background-color: black;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    padding: 10px;
    opacity: 0.8;
}